'use client';
import React, { useEffect, useRef } from 'react';
import Image from 'next/image';
import styles from './ExpandingSection.module.css';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const ExpandingSection = () => {
  const contentRef = useRef<HTMLDivElement>(null);
  const imagesRef = useRef<(HTMLDivElement | null)[]>([]);

  const articles = [
    { id: 1, title: "Cartier Celebrates 100 Years of Trinity", image: "/watches.jpg" },
    { id: 2, title: "Trinity: A Contemporary Icon", image: "/caps.jpg" },
    { id: 3, title: "All Linked by Trinity", image: "/perfumes.jpg" },
    { id: 4, title: "Trinity, A Symbol of Unity", image: "/sunglasses.jpg" },
    { id: 5, title: "Trinity 100, The Journey of Trinity", image: "/jackets.jpg" }
  ];

  useEffect(() => {
    if (!contentRef.current) return;

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: contentRef.current,
        start: 'top bottom',
        end: 'top center',
        scrub: 1,
      },
    });

    // Content animation
    tl.fromTo(contentRef.current,
      { y: 50, opacity: 0.7 },
      { y: 0, opacity: 1, duration: 1 }
    );

    // Images animation
    tl.fromTo(imagesRef.current,
      { y: 30, opacity: 0 },
      { 
        y: 0, 
        opacity: 1,
        stagger: 0.1,
        ease: 'power2.out'
      },
      "-=0.5"
    );

    return () => ScrollTrigger.getAll().forEach(t => t.kill());
  }, []);

  return (
    <div ref={contentRef} className={styles.wrapper}>
      <div className={styles.content}>
        <p className={styles.caption}>CHAPTER ONE</p>
        <h2 className={styles.title}>3 <i>IS THE</i> MAGIC NUMBER</h2>
        
        <div className={styles.grid}>
          {articles.map((article, i) => (
            <div 
              key={article.id}
              ref={el => imagesRef.current[i] = el}
              className={styles.item}
            >
              <div className={styles.imageContainer}>
                <Image
                  src={article.image}
                  alt=""
                  fill
                  className={styles.image}
                  sizes="(max-width: 768px) 100vw, 50vw"
                />
              </div>
              <p className={styles.label}>{article.title}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ExpandingSection;